<the-card class="main" :frame="[&quot;head&quot;]"><template v-slot:head-left><a-form class="search-form" layout="inline" :model="searchForm"><a-form-item label="" name="teacherId" v-if="isAdmin"><a-select class="search-input" v-model:value="searchForm.teacherId" placeholder="" style="width: 150px" allowClear show-search :default-active-first-option="false" @change="handleSearchClass(&quot;&quot;)" :filterOption="false"><a-select-option v-for="v in selectTeachers" :key="v.code" :value="v.code">{{ v.name }}</a-select-option></a-select></a-form-item></a-form></template><template v-slot:head-right><div class="search-btn flex flex-row align-items-center"><a-config-provider :auto-insert-space-in-button="false"><a-button class="s-btn btns-width-80 btns-radius-06" type="primary" style="margin-right: 16px" @click="searchList"></a-button></a-config-provider><a-config-provider :auto-insert-space-in-button="false"><a-button class="s-btn btns-width-80 btns-radius-06" type="primary" ghost @click="resetFields"></a-button></a-config-provider></div></template></the-card>